$sectionbg: #F8F8F8;

$background: #dc4900;

$navbgcolor : #f1f1f1;

$firstbggradientcolor: #dc4900;

$secondbggradientcolor: lighten($firstbggradientcolor, 10%);

$landingBg: #dc4900;

$bg-text-color: #ffffff;

// Mixins
@mixin linear-background-color($param1, $param2)
{
	background: $param1; 
	background: -webkit-linear-gradient($param1 70%,$param2 95%,$param1); /* Safari 5.1-6.0 */
    background: -o-linear-gradient($param1 70%,$param2 95%,$param1); /* Opera 11.1-12.0 */ 
    background: -moz-linear-gradient($param1 70%,$param2 95%,$param1); /* Firefox 3.6-15 */
	background: linear-gradient($param1 70%,$param2 95%,$param1);
} 

@mixin radial-background-color($param1, $param2)
{
	background: $param1; /* For browsers that do not support gradients */
	background: -webkit-radial-gradient(circle, $param2, $param1); /* Safari */
	background: -o-radial-gradient(circle, $param2, $param1); /* Opera 11.6 to 12.0 */
	background: -moz-radial-gradient(circle, $param2, $param1); /* Firefox 3.6 to 15 */
	background: radial-gradient(circle, $param2, $param1); /* Standard syntax */	
}

@mixin flex-align($param)
{
	justify-content : $param;
	-webkit-justify-content : $param;
	-ms-justify-content : $param;
	-moz-justify-content : $param;
	-o-justify-content : $param;
}

@mixin small{
	@media only screen and (max-width: 767px){
		@content;
	}

}

@mixin not-small{
	@media only screen and (min-width: 767px){
		@content;
	}
}
/*Table Upper Case*/

table{
	td.uppercase,
	th.uppercase{
		text-transform: uppercase;
	}
}

.uppercase{
	text-transform: uppercase !important;
}


.printonly{
	 display : none;
	}

@media print{
	.printnone{
	 display : none !important;
	}

	.printonly{
	 display : block !important;
	}

	table.transfer{
		margin-top: 0px !important;
		width: 720px;
	}
	table.transfer.special{
		margin-top: 20px !important;
	}

	.print-margin-top{
		margin-top: 20px;
	}

	section#unity-exam-dashboard{
		margin-top: 0px !important;
	}

}
